.SearchBar .searchbar-wrapper {
    padding-bottom: 30px;
}

.SearchBar .searchbar-wrapper input {
    padding: 2px 6px;
    border-radius: 100px;
    border: solid 1px #e3e3e3;
    width: calc(100% - 12px)
}


.SearchBar .add-wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.SearchBar .label {
    -ms-flex: 1 1;
        flex: 1 1;
}

.SearchBar .add-button {
    -ms-flex: 0 0;
        flex: 0 0;
}



